<template>
  <div id="edit-project">
    <h3>New Project</h3>
    <div class="row">
      <form @submit.prevent="updateProject" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="project_id" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="owner" required>
          </div>
        </div>
        <button type="submit" class="btn green">Submit</button>
        <router-link to="/projects" class="btn red">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-project',
  data () {
    return {
      project_id: null,
      name: null,
      owner: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('projects').where('project_id', '==', to.params.project_id).get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.project_id = doc.data().project_id
            vm.name = doc.data().name
            vm.owner = doc.data().owner
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.project_id = doc.data().project_id
            this.name = doc.data().name
            this.owner = doc.data().owner
          })
        })
    },
    updateProject () {
      db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              project_id: this.project_id,
              name: this.name,
              owner: this.owner
            })
              .then(() => {
                this.$router.push({name: 'view-project', params: {project_id: this.project_id}})
              })
          })
        })
    }
  }
}
</script>



