<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Companies</h4></li>
      <li v-for="company in companies" v-bind:key="company.id" class="collection-item">
        <div class="chip">{{company.company_id}}</div>
        {{company.name}} : {{company.contact}}
        <router-link class="secondary-content" v-bind:to="{name: 'view-company', params: {company_id: company.company_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      companies: []
    }
  },
  created () {
    db.collection('companies').orderBy('company_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'company_id': doc.data().company_id,
          'name': doc.data().name,
          'contact': doc.data().contact
        }
        this.companies.push(data)
      })
    })
  }
}
</script>



