<template>
  <div id="view-company">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4>
      </li>
      <li class="collection-item">Company ID: {{company_id}}</li>
      <li class="collection-item">Contact: {{contact}}</li>
    </ul>
    <router-link to="/companies" class="btn green">Back</router-link>
    <button @click="deleteCompany" class="btn red">Delete</button>
    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-company', params: {company_id: company_id}}" class="btn-floating btn-large green">
      <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-company',
  data () {
    return {
      company_id: null,
      name: null,
      contact: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('companies').where('company_id', '==', to.params.company_id).get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.company_id = doc.data().company_id
            vm.name = doc.data().name
            vm.contact = doc.data().contact
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('companies').where('company_id', '==', this.$route.params.company_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.company_id = doc.data().company_id
            this.name = doc.data().name
            this.contact = doc.data().contact
          })
        })
    },
    deleteCompany () {
      if (confirm('Are you sure?')) {
        db.collection('companies').where('company_id', '==', this.$route.params.company_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
      }
    }
  }
}
</script>



