<template>
  <div id="view-project">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4>
      </li>
      <li class="collection-item">Projekt ID: {{project_id}}</li>
      <li class="collection-item">Owner: {{owner}}</li>
    </ul>
    <router-link to="/projects" class="btn green">Back</router-link>
    <button @click="deleteProject" class="btn red">Delete</button>
    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-project', params: {project_id: project_id}}" class="btn-floating btn-large green">
      <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-project',
  data () {
    return {
      project_id: null,
      name: null,
      owner: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('projects').where('project_id', '==', to.params.project_id).get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.project_id = doc.data().project_id
            vm.name = doc.data().name
            vm.owner = doc.data().owner
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.project_id = doc.data().project_id
            this.name = doc.data().name
            this.owner = doc.data().owner
          })
        })
    },
    deleteProject () {
      if (confirm('Are you sure?')) {
        db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
      }
    }
  }
}
</script>



