<template>
  <div id="new-project">
    <h3>New Project</h3>
    <div class="row">
      <form @submit.prevent="saveProject" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="project_id" required>
            <label>Project ID</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="owner" required>
            <label>Owner</label>
          </div>
        </div>
        <button type="submit" class="btn green">Submit</button>
        <router-link to="/projects" class="btn red">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-project',
  data () {
    return {
      project_id: null,
      name: null,
      owner: null,
      company_id: null
    }
  },
  methods: {
    saveProject () {
      db.collection('projects').add({
        project_id: this.project_id,
        name: this.name,
        owner: this.owner,
        company_id: this.company_id
      })
        .then(docRef => {
          console.log('Project added: ', docRef.id)
          this.$router.push('/')
        })
        .catch(error => {
          console.error('Error adding project: ', error)
        })
    }
  }
}
</script>



