<template>
  <div id="edit-company">
    <h3>New Company</h3>
    <div class="row">
      <form @submit.prevent="updateCompany" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="company_id" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="contact" required>
          </div>
        </div>
        <button type="submit" class="btn green">Submit</button>
        <router-link to="/companies" class="btn red">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-company',
  data () {
    return {
      company_id: null,
      name: null,
      contact: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('companies').where('company_id', '==', to.params.company_id).get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.company_id = doc.data().company_id
            vm.name = doc.data().name
            vm.contact = doc.data().contact
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('companies').where('company_id', '==', this.$route.params.company_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.company_id = doc.data().company_id
            this.name = doc.data().name
            this.contact = doc.data().contact
          })
        })
    },
    updateCompany () {
      db.collection('companies').where('company_id', '==', this.$route.params.company_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              company_id: this.company_id,
              name: this.name,
              contact: this.contact
            })
              .then(() => {
                this.$router.push({name: 'view-company', params: {company_id: this.company_id}})
              })
          })
        })
    }
  }
}
</script>



