<template>
  <div id="new-company">
    <h3>New Company</h3>
    <div class="row">
      <form @submit.prevent="saveCompany" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="company_id" required>
            <label>Company ID</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="contact" required>
            <label>Contact</label>
          </div>
        </div>
        <button type="submit" class="btn green">Submit</button>
        <router-link to="/companies" class="btn red">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-company',
  data () {
    return {
      company_id: null,
      name: null,
      contact: null
    }
  },
  methods: {
    saveCompany () {
      db.collection('companies').add({
        company_id: this.company_id,
        name: this.name,
        contact: this.contact
      })
        .then(docRef => {
          console.log('Company added: ', docRef.id)
          this.$router.push('/')
        })
        .catch(error => {
          console.error('Error adding Company: ', error)
        })
    }
  }
}
</script>



