<template>
  <div id="dashboardprojects">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Projects</h4></li>
      <li v-for="project in projects" v-bind:key="project.id" class="collection-item">
        <div class="chip">{{project.project_id}}</div>
        {{project.name}} : {{project.contact}}
        <router-link class="secondary-content" v-bind:to="{name: 'view-project', params: {project_id: project.project_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboardprojects',
  data () {
    return {
      projects: []
    }
  },
  created () {
    db.collection('projects').orderBy('project_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'project_id': doc.data().company_id,
          'name': doc.data().name,
          'owner': doc.data().owner,
          'company_id': doc.data().company_id
        }
        this.projects.push(data)
      })
    })
  }
}
</script>



